import { createContext, useState, useEffect } from "react";
import data1 from "../../data";

var Buffer = require("buffer/").Buffer;

const Context = createContext();

export const ContextProvider = ({ children }) => {
  const [loading, setLoading] = useState(true);

  const fetchList = async () => {
    const response = await fetch(
      `https://api.github.com/repos/suryo811/blog/contents/data.json`
    );
    let data = await response.json();
    let contentCode = data.content;
    let utf8encoded = Buffer.from(contentCode, "base64").toString("utf8");
    // console.log(utf8encoded);
    let arrayData = JSON.parse(utf8encoded);

    setList(arrayData);
    setLoading(false);
  };

  useEffect(() => {
    fetchList();
  }, []);

  const [list, setList] = useState(data1);

  return (
    <Context.Provider value={{ list, loading }}>{children}</Context.Provider>
  );
};
export default Context;
