import React from "react";

function BlogCard({ item }) {
  return (
    <div className="blogs--post--div--topic">
      <h4 className="blogs--post--div--topic--heading">
        <a href={`blogs/${item.year}/${item.id}`}>{item.heading}</a>
      </h4>
      <h4 className="blogs--post--div--topic--date">
        {item.year}-
        {item.month.toString().length > 1
          ? item.month
          : item.month.toString().padStart(2, "0")}
        -
        {item.date.toString().length > 1
          ? item.date
          : item.date.toString().padStart(2, "0")}
      </h4>
    </div>
  );
}

export default BlogCard;
