import React from "react";
import { useContext } from "react";
import Context from "../components/Context/Context";
import { useParams } from "react-router-dom";
const readingTime = require("reading-time");

function Blog() {
  const { id } = useParams();
  let { list } = useContext(Context);
  const stats = readingTime(list[id - 1].body);

  return (
    <main>
      <div className="blog">
        <article className="blog--article">
          <h1 className="blog--article-title">{list[id - 1].heading}</h1>
          <div className="blog--article-info">
            <div className="blog--article-info-detail date">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="blog--icon feather-calendar"
              >
                <rect x="3" y="4" width="18" height="18" rx="2" ry="2"></rect>
                <line x1="16" y1="2" x2="16" y2="6"></line>
                <line x1="8" y1="2" x2="8" y2="6"></line>
                <line x1="3" y1="10" x2="21" y2="10"></line>
              </svg>
              <p>
                Posted: {list[id - 1].year}-
                {list[id - 1].month.toString().length > 1
                  ? list[id - 1].month
                  : list[id - 1].month.toString().padStart(2, "0")}
                -
                {list[id - 1].date.toString().length > 1
                  ? list[id - 1].date
                  : list[id - 1].date.toString().padStart(2, "0")}
              </p>
            </div>
            <div className="blog--article-info-detail time">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="blog--icon feather-clock"
              >
                <circle cx="12" cy="12" r="10"></circle>
                <polyline points="12 6 12 12 16 14"></polyline>
              </svg>
              <p>{Math.round(stats.minutes)} minutes</p>
            </div>
          </div>
          <div className="blog--article-content">
            <p>{list[id - 1].body}</p>
          </div>
        </article>
        <div className="blog--article-info-page">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
            className="blog--icon feather-file-text"
          >
            <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path>
            <polyline points="14 2 14 8 20 8"></polyline>
            <line x1="16" y1="13" x2="8" y2="13"></line>
            <line x1="16" y1="17" x2="8" y2="17"></line>
            <polyline points="10 9 9 9 8 9"></polyline>
          </svg>
          <p>{stats.words} Words</p>
        </div>
      </div>
    </main>
  );
}

export default Blog;
